import { Promise } from 'core-js';

const onError = (res) => {
  if (res.ok) {
    return res.json();
  }
  //  ,  
  return Promise.reject(`: ${res.status}`);
};

export class Api {
  constructor(options) {
    //  
    this._url = options.baseUrl;
    this._headers = options.headers;
  }

  getInitialCards() {
    return fetch(`${this._url}/cards`, {
      method: 'GET',
      headers: this._headers,
    }).then(onError);
  }

  getInitialUsers() {
    return fetch(`${this._url}/users/me`, {
      method: 'GET',
      headers: this._headers,
    }).then(onError);
  }

  //  
  editUsers(user) {
    return fetch(`${this._url}/users/me`, {
      method: 'PATCH',
      headers: this._headers,
      body: JSON.stringify({
        name: user.name,
        about: user.about,
      }),
    }).then(onError);
  }

  //  
  editAvatar(userAvatar) {
    return fetch(`${this._url}/users/me/avatar`, {
      method: 'PATCH',
      headers: this._headers,
      body: JSON.stringify({
        avatar: userAvatar.avatar,
      }),
    }).then(onError);
  }

  getAllData() {
    return Promise.all([this.getInitialCards(), this.getInitialUsers()]);
  }
  //   
  createCard(data) {
    return fetch(`${this._url}/cards`, {
      method: 'POST',
      headers: this._headers,
      body: JSON.stringify({
        name: data.name,
        link: data.link,
      }),
    }).then(onError);
  }

  //  
  deleteCard(data) {
    return fetch(`${this._url}/cards/${data._id}`, {
      method: 'DELETE',
      headers: this._headers,
    }).then(onError);
  }

  //  
  likeCard(data) {
    return fetch(`${this._url}/cards/${data._id}/likes`, {
      method: 'PUT',
      headers: this._headers,
    }).then(onError);
  }

  //  
  deleteLikeCard(data) {
    return fetch(`${this._url}/cards/${data._id}/likes`, {
      method: 'DELETE',
      headers: this._headers,
    }).then(onError);
  }
}
